.previewItem {
  width: 250px;
  height: 150px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 10px;
  align-items: center;
  box-shadow: -2px 5px 10px -3px rgba(156, 156, 156, 0.721);
  position: relative;
  border-radius: 5px;

  &__imgContainer {
    background-color: brown;
    height: 130px;
    width: 130px;
    border-radius: 50%;
    position: absolute;
    bottom: 50%;
    overflow: hidden;

    &--img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    text-transform: uppercase;
    font-weight: 700;
    text-align: center;
    width: 80%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    color: black;
  }

  &__author {
    margin-bottom: 15px;
    color: rgb(114, 114, 114);
    font-weight: 600;
    text-align: center;

    width: 50%;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
  }
}
